html {
  scroll-behavior: smooth;
}

.container{
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
}

.display{
  display: flex;
  flex-direction: row;
  align-items: center;
  flex-basis: auto;
  justify-content:space-around;
}

.img{
  width: 200px;
  height: auto;
  border-radius: 2rem;
}

